@use './base' as *;

body {
  background: url("../images/bg-intro-mobile.png")  center $red;
  @include query(md) {
    height: 100vh;
    background: url("../images/bg-intro-desktop.png")  center $red;
  }
}
.form-section {
  max-width: 72rem;
  padding: 3rem 0;
  margin: auto;
  display: grid;
  grid-template-columns: 5% 1fr 5%;
  grid-template-rows: auto;
  place-items: center;
  gap: 3.5rem 1rem;

  grid-template-areas:
    ".  title    ."
    ". container ."
    ".  footer   .";

  @include query(md) {
    grid-template-columns: 15% 1fr 15%;
  }
  @include query(lg-2) {
    height: 100vh;
    gap: 0 5rem;
    padding: 3rem;
    background: url("../images/bg-intro-desktop.png") no-repeat center $red;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      "title  container"
      "title  container"
      "footer  footer  ";
  }

  .title {
    grid-area: title;
    display: grid;
    place-items: center;
    gap: 1rem;
  }

  .container {
    grid-area: container;
  }

  .footer {
    grid-area: footer;
  }
}
