@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
}

input[type=submit] {
  display: none;
}

::-ms-reveal {
  display: none;
}

button {
  border: none;
  background-color: white;
}

ul {
  list-style: none;
  text-decoration: none;
}
ul li a {
  display: inline-block;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.button-submit {
  cursor: pointer;
  padding: 1rem;
  border-radius: 0.3rem;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.button-submit:hover {
  opacity: 0.7;
}

.container {
  width: 100%;
  display: grid;
  gap: 1.5rem;
}
.container .head {
  padding: 1rem 4rem;
  border-radius: 0.6rem;
  box-shadow: rgba(0, 0, 0, 0.1) 1.95px 1.95px 2.6px;
}
.container .form {
  border-radius: 0.6rem;
  padding: 1.5rem;
  display: grid;
  gap: 1.5rem;
}
.container .form__control {
  display: grid;
  gap: 0.5rem;
}
.container .form__control-input {
  position: relative;
  display: grid;
}
.container .form__input {
  padding: 1rem;
  opacity: 0.8;
  border-radius: 0.3rem;
  border: 0.11rem solid #b9b6d3;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.container .form__error, .container .form__validation {
  display: none;
  position: absolute;
  right: 0.6rem;
  top: 50%;
  transform: translate(-50%, -50%);
}
.container .form__input-error {
  text-align: end;
  display: none;
}
.container .form__input-error-active {
  display: block;
}
.container .form__successful-message {
  display: none;
}
.container .form__successful-message-active {
  display: block;
}
.container .form__error-message {
  display: none;
}
.container .form__error-message-active {
  display: block;
}
.container .form__control-correct .form__error {
  display: block;
}
.container .form__control-correct .form__input {
  border: 0.11rem solid #38cc8c;
}
.container .form__control-incorrect .form__error {
  display: block;
}
.container .form__control-incorrect .form__input {
  border: 0.11rem solid #ff7a7a;
}

body {
  background: url("../images/bg-intro-mobile.png") center #ff7a7a;
}
@media (min-width: 48em) {
  body {
    height: 100vh;
    background: url("../images/bg-intro-desktop.png") center #ff7a7a;
  }
}

.form-section {
  max-width: 72rem;
  padding: 3rem 0;
  margin: auto;
  display: grid;
  grid-template-columns: 5% 1fr 5%;
  grid-template-rows: auto;
  place-items: center;
  gap: 3.5rem 1rem;
  grid-template-areas: ".  title    ." ". container ." ".  footer   .";
}
@media (min-width: 48em) {
  .form-section {
    grid-template-columns: 15% 1fr 15%;
  }
}
@media (min-width: 60em) {
  .form-section {
    height: 100vh;
    gap: 0 5rem;
    padding: 3rem;
    background: url("../images/bg-intro-desktop.png") no-repeat center #ff7a7a;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "title  container" "title  container" "footer  footer  ";
  }
}
.form-section .title {
  grid-area: title;
  display: grid;
  place-items: center;
  gap: 1rem;
}
.form-section .container {
  grid-area: container;
}
.form-section .footer {
  grid-area: footer;
}

